@mixin font($size, $height: $size, $weigh: bold, $style: normal) {
  font-size: $size;
  font-style: $style;
  font-weight: $weigh;
  line-height: $height;
}

@mixin media-laptop{
  @media (max-width: $content-width) { @content; }
} 

@mixin media-tablet {
  @media (max-width: $tablet-width) { @content; }
}

@mixin media-mobile {
  @media (max-width: $mobile-width) { @content; }
}